a:visited { text-decoration: none; }

a:active { text-decoration: none; }

a:focus { text-decoration: none; }

body { height: 100%; font: 14px/24px 'Open Sans', sans-serif; background-color: #e8e8e8; }

.wrapper { width: 1240px; margin: 0 auto; padding: 0 20px; margin-top: 30px; }

.new-note { display: flex; flex-direction: column; max-width: 300px; margin: 0 auto; }

.new-note__head { padding: 4px; }

.new-note__text { margin: 10px 0; resize: none; height: 100px; padding: 4px; }

.note-list { display: flex; flex-direction: row; flex-wrap: wrap; margin-top: 20px; }

.note-list__item { width: 350px; border: 1px solid #000; background-color: #fff; padding: 5px; position: relative; margin-bottom: 20px; margin-right: 75px; }

.note-list__item:nth-child(3n+3) { margin-right: 0; }

.note-list__head { font-size: 18px; margin: 0; }

.note-list__text { font-size: 14px; }

.note-list__del { position: absolute; top: 2px; right: 8px; font-weight: bold; cursor: pointer; }
